import { Button, Card, List, ListItem, Typography } from "@mui/material";
import React from "react";
import { useNavigate } from "react-router-dom";


export default function Home() {
    const navigate = useNavigate()

    return (
        <>
            <Card sx={{ p: 20, m: 20 }}>
                <div style={{ display: "flex", justifyContent: "space-between", alignItems: "center", flexDirection: "column" }}>
                    <div>
                        <Typography variant="h2" gutterBottom>
                            Mahir Atolye
                        </Typography>
                    </div>
                    <div style={{ display: "flex", alignItems: "flex-start" }}>
                        <List>
                            <ListItem>
                                <Button variant="contained" onClick={() => { navigate('/envanter') }}>
                                    Envanter Sistemi
                                </Button>
                            </ListItem>
                            <ListItem>
                                <Button variant="contained" onClick={() => { navigate('/task') }}>
                                    Task Sistemi
                                </Button>
                            </ListItem>
                            <ListItem>
                                <Button variant="contained" onClick={() => { navigate('/sheet') }}>
                                    Tablo Sistemi
                                </Button>
                            </ListItem>
                        </List>
                    </div>
                </div>
            </Card>
        </>
    )
}
